.row{
    margin-left: 20px;
    color: white;
}
.row_poster{
    /* width: 100%; */
    object-fit: contain;
    max-height: 100px;
    margin-right: 10px;
   transition: transform 450ms;
   /* overflow-y: hidden;
   overflow-x: scroll; */
}

.row_posters{
    display: flex;
    overflow-y: hidden;
    /* overflow-x: scroll; */
    padding: 20px;
}
.row_posters::-webkit-scrollbar{
    display: none;
}
.row_poster:hover{
 transform:  scale(1.08);
}
.Large_row_poster{
    max-height: 250px;
}
.Large_row_poster:hover{
    transform:  scale(1.09);
   }